import React, {useState} from 'react';
import {Nav, Navbar} from "react-bootstrap";
import "./Navibar.css"
import {dark} from "@material-ui/core/styles/createPalette";
import {red} from "@material-ui/core/colors";
import {Link} from "react-router-dom";

const Navibar =()=> {

    const [bg,setbg]=useState('transparent')

    document.addEventListener('scroll', () => {
        if (window.scrollY >250) {
           setbg("dark");
        } else {
           setbg("transparent");
        }
    });

        return (
            <div>
                <Navbar fixed={"top"} className={"navbar"} collapseOnSelect expand="lg" bg={bg} variant="dark">
                    <Navbar.Brand  className={"tittle"}><h1><span>C</span>hanaka &nbsp;&nbsp;<span >H</span>erath</h1></Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="links">
                           <Navbar.Brand><a href={"#home"}>Home</a> </Navbar.Brand>
                            <Navbar.Brand><a href={"#services"}>Services</a> </Navbar.Brand>
                            <Navbar.Brand><a href={"#projects"}>Projects</a> </Navbar.Brand>
                            <Navbar.Brand><a href={"#about"}>About</a> </Navbar.Brand>
                            <Navbar.Brand><a href={"#contact"} >Contact</a></Navbar.Brand>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </div>
        );
}

export default Navibar;